@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,300;0,400;0,500;0,700;0,900;1,400;1,500;1,700;1,900&display=swap');

body {
  margin: 0;
  background: hsl(0, 0%, 98%);
  font-family: 'Roboto', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

a {
  text-decoration: none;
}

.main-container {
  margin: 0 auto;
  padding: 0 20px;
  max-width: 1200px;
}

.title {
  color: #6b6b6b;
  text-align: center;
  margin-bottom: 32px;
  text-transform: capitalize;
}

/* form */
#form label {
  color: grey;
  font-weight: bold;
}

#form label,
#form input {
  display: block;
  margin-bottom: 12px;
}

#form input,
#form select {
  height: 32px;
  padding: 0 12px;
  margin-bottom: 20px;
  border-radius: 99px;
  border: 2px solid grey;
  transition: all 0.3s ease;
  width: clamp(220px, 220px, 220px);
}

#form textarea {
  padding: 12px;
  margin-bottom: 20px;
}

.btn-submit {
  color: #fff;
  display: flex;
  font-size: 16px;
  cursor: pointer;
  padding: 8px 12px;
  border-radius: 6px;
  width: max-content;
  margin-bottom: 10px;
  border: 2px solid transparent;
  background: hsl(342, 87%, 58%);
}