* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

img {
  width: 100%;
}

.card-wrap {
  gap: 20px;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  margin-bottom: 60px;
  justify-content: space-around;
}

.coin-wrap .title {
  display: block;
  color: red;
}

.card {
  padding: 14px;
  display: flex;
  min-width: 280px;
  min-height: 200px;
  background: #fff;
  border-radius: 8px;
  flex-direction: column;
  justify-content: center;
  -webkit-box-shadow: 0 0.125rem 0.25rem rgb(32 32 32 / 10%);
  box-shadow: 0 0.125rem 0.25rem rgb(32 32 32 / 10%);
}

.card .head {
  gap: 14px;
  display: flex;
  margin-bottom: 20px;
  align-items: center;
}

.card .icon {
  max-width: 50px;
}

.coin {
  color: #6b6b6b;
}

.coin-name {
  font-size: 18px;
  margin-bottom: 4px;
}

.coin-price {
  font-size: 16px;
}

.vote,
.vote-count {
  color: #fff;
  display: block;
  font-weight: 500;
  padding: 8px 4px;
  text-align: center;
  margin-bottom: 6px;
  background: #ed2a6c;
}

.vote-count {
  background: #b32aec;
}

.details-link {
  display: block;
  margin-top: 20px;
  text-align: center;
}